import { createTheme, ThemeProvider } from '@mui/material/styles';

import { Grid, IconButton, InputAdornment, OutlinedInput, Snackbar } from '@mui/material';
import queryString from 'query-string';
import { useState, useEffect } from 'react';
import { CopyAll } from '@mui/icons-material';

const darkTheme = createTheme({
  palette: {
    mode: 'dark'
  }
});

const Page = () => {
  const [copy, setCopy] = useState(false);
  const [value, setValue] = useState(null);

  useEffect(() => {
    document.title = `LsCo Software - Token Callback`;

    if (!location.search.trim()) {
      const parsed = queryString.parse(location.hash);

      setValue(parsed.access_token);
    } else if (new URLSearchParams(location.search).has('code')) {
      const parsed = queryString.parse(location.search);

      setValue(parsed.code);
    } else if (new URLSearchParams(location.search).has('token')) {
      const parsed = queryString.parse(location.search);

      setValue(parsed.token);
    }

    history.pushState(null, null, '/');
  }, []);

  const handleClickCopy = () => {
    navigator.clipboard.writeText(value);
    setCopy(true);
  };

  const handleCloseSnackbar = () => {
    setCopy(false);
    window.close();
  };

  return (
    <Grid container justifyContent="center">
      <Grid item xs={12} sm={6} md={4} sx={{ m: 2.5 }}>
        <ThemeProvider theme={darkTheme}>
          <OutlinedInput
            fullWidth
            sx={
              value
                ? {
                    '&&&': {
                      input: {
                        filter: 'blur(2.5px)',
                        caretColor: 'transparent',
                        textAlign: 'center'
                      }
                    }
                  }
                : {
                    '&&&': {
                      input: {
                        textAlign: 'center'
                      }
                    }
                  }
            }
            type="text"
            inputProps={{ style: { userSelect: 'all' } }}
            value={value ?? 'Access Token was not specified'}
            disabled
            endAdornment={
              value ? (
                <InputAdornment position="start">
                  <IconButton aria-label="copy token" onClick={handleClickCopy} edge="end">
                    <CopyAll />
                  </IconButton>
                </InputAdornment>
              ) : (
                ''
              )
            }
            label="Password"
          ></OutlinedInput>
        </ThemeProvider>
      </Grid>
      <Snackbar open={copy} autoHideDuration={2000} onClose={handleCloseSnackbar} message="Token copied to clipboard!" />
    </Grid>
  );
};

export default Page;
